function mainSlider() {
    var swiper = new Swiper('.main-slider.swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        speed: 800,
        effect: 'fade',
        allowTouchMove: false,
        autoplay:{
            delay: 6000
        },
        fadeEffect: {
            crossFade: true
        },

    })
}

function mainInfoSlider() {
    var swiper = new Swiper('.main-info .swiper-container', {
        slidesPerView: 4,
        spaceBetween: 0,
        autoplay:{
            delay: 3000
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
            },
            620: {
                slidesPerView: 3,
            },
            820: {
                slidesPerView: 4,
            },
        }
    })
}

function collectionPortfolioSlider() {
    var swiper = new Swiper('.collection-portfolio .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 40,
        loop: true,
        loopAdditionalSlides: 10,
        navigation: {
            nextEl: '.collection-portfolio .swiper-button-next',
            prevEl: '.collection-portfolio .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: "auto",
                spaceBetween: 20,
            },
            820: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            992: {
                spaceBetween: 40,
                slidesPerView: 3,
            },
        }
    })
}

function collectionGallerySlider() {
    var swiper = new Swiper('.collection-gallery .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 20,
        loop: true,
        navigation: {
            nextEl: '.collection-gallery .swiper-button-next',
            prevEl: '.collection-gallery .swiper-button-prev',
        },
        breakpoints: {
            // 320: {
            //     slidesPerView: 1,
            // },
            // 450: {
            //     slidesPerView: 2,
            // },
        }
    })
}

mainSlider()
mainInfoSlider()
collectionPortfolioSlider()
collectionGallerySlider()
